*
{
  margin:0;
  padding: 0;
}
body
{
 background: url(https://static.vecteezy.com/system/resources/previews/000/625/480/original/vector-abstract-geometric-background-in-light-colors.jpg);
 font-family: Georgia, 'Times New Roman', Times, serif;
 display: flex;
 justify-content: center;
 align-items: center;
 min-height: 100vh;
}
h1
{
  text-align: center;
  margin-bottom: 50px;
  font-size: 36px;
  color: rgb(31, 54, 95);
}
.app
{
  background: linear-gradient(199deg,#9bf0eb 0%, #e7ad70 80%);
  width: 100%;
  min-height: 300px;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 9px 12px 32px -12px rgba(0,0,0,0.75);;
  display: flex;
  justify-content: space-evenly;
}
 .Question-Section
 {
  width:100%;
  position: relative;
 }
 .Question-count
 {
  margin-bottom: 20px;
 }
 /* .Question-count span{
  font-size: 28px;
 } */
 .Question-text
{
  margin-bottom: 12px;
  color: rgb(92, 55, 7);
  font-size: 26px;
} 
.Answer-Section 
{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;


}
 button
{
  width: 100%;
  font-size: 20px;
  color:  rgb(92, 55, 7);
  
  background: linear-gradient( 199deg,#9bf0eb 0%, #e7ad70 80%);
  border-radius: 5px;
  display: flex;
  padding: 10px;
  justify-content: flex-start;
  align-items: center;
  border: 2px solid rgb(69, 142, 69);
  cursor: pointer;
 
}

button:hover{
  background-color: #2eb9ce;
}
/* button:focus
{
 outline: none;
} */
.score-card
{
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(99, 63, 86);
  font-size: 30px;
  font-weight: bold;
}